.project {
	display: flex;
	flex-direction: column;
	margin: 1rem;
	background-color: white;
	border-radius: 5px;
	box-shadow: 0 50px 100px rgb(50 50 93 / 5%), 0 15px 35px rgb(50 50 93 / 10%),
		0 5px 15px rgb(0 0 0 / 10%);
}

.right-project {
	text-align: left;
	display: flex;
	flex-direction: column;
}

.left-project img {
	max-width: 100%;
	height: auto;
	border-top-right-radius: 5px;
	border-top-left-radius: 5px;
}

.title {
	font-weight: 500;
	font-size: 1.5 rem;
	line-height: 38px;
	color: #525f7f;
	margin: 0.25rem 0.75rem;
}

.about {
	line-height: 27px;
	color: #525f7f;
	font-size: 1rem;
	margin: 0 0.75rem;
}

.tags {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	margin: 1rem 0;
}

.tag {
	margin: 0.25rem 0.25rem;
	margin-left: 0.75rem;
	border-radius: 4px;
	border: 2px solid #92eac0;
	font-size: 0.85rem;
	color: #525f7f;
	padding: 0.5rem;
}

.buttons {
	margin: 0 0.75rem;
	margin-bottom: 1rem;
}

.demo {
	background-color: #747fe0;
	color: white;
	margin-right: 1rem;
}

.source {
	background-color: white;
	color: #747fe0;
}

button {
	font-weight: bold;
	border-radius: 5px;
	box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
	cursor: pointer;
	border: 0;
	padding: 0.5rem;
	transition: 0.5s;
}

button:hover {
	transform: translateY(-0.25em);
	transition: 0.5s;
}

.demo:hover {
	opacity: 0.8;
}

.demo .icon {
	margin-left: 0.5rem;
}

.source .icon {
	margin-right: 0.5rem;
}
